  getAllCampaigns() {
    this.campService.getAllCampaigns({}, this.currentUser.id).then((res) => {
      this.campList = res.data.filter(campaign => campaign.status !== 'COMPLETE');
    }, (err) => {
      if (err.error.status == 404) {
        this.campList = [];
      }
    })
  }

  searchFunc(){
    if (this.searchTerm.trim() === '') {
      this.getAllCampaigns();
    } else {
      // this.campService.getAllCampaigns({}, this.currentUser.id).then((res:any) => {
        this.campList = this.campList.filter(item => {
          return (
            (item.name && item.name.toLowerCase().includes(this.searchTerm.toLowerCase())) ||
            (item.description && item.description.toLowerCase().includes(this.searchTerm.toLowerCase()))
          );
        });
      // });
      if(this.campList.length === 0){
        this.toastService.toastMsg({
          title: "Error",
          content: this.searchTerm + " Is Not Found",
        });
        // this.toastService.openErrorSnackBar(this.searchTerm + " is not found");
      }
    }
  }

  deleteCamp(campaignId : string){
    this.campIdList.push(campaignId);
  }

  deleteCampaigns() {
    this.campService.deleteCampaigns({ "deleteArray": this.campIdList }, this.currentUser.id).then((res) => {
      if(res.success){
        this.getAllCampaigns()
        this.toastService.toastMsg({
          title: "Success",
          content: "Campaign Deleted Successfully!!!"
        });
        // this.toastService.openSnackBar('Campaign deleted successfully');
      }
    }, (err) => {
      if (err.error.status == 404) {
        this.campList = [];
      }
    })
  }
 
