import { Component, Inject, OnInit } from '@angular/core';
import { MAT_DIALOG_DATA, MatDialogRef } from '@angular/material/dialog';

@Component({
  selector: 'app-user-list-names',
  templateUrl: './user-list-names.component.html',
  styleUrls: ['./user-list-names.component.scss']
})
export class UserListNamesComponent implements OnInit {
  public campaignUsers=[]

  constructor(public dialogRef: MatDialogRef<UserListNamesComponent>, @Inject(MAT_DIALOG_DATA) public campaignData: any, ) { }

  ngOnInit(): void {
    this.campaignUsers =this.campaignData.campaignUser;
    console.log("campaignUsers",this.campaignUsers);
  }
  cancel(){
    this.dialogRef.close();
  }

}
