import { Component, Inject, OnInit } from '@angular/core';
import { ExpenseAddComponent } from '../../expense/expense-add.component';
import { MAT_DIALOG_DATA, MatDialog, MatDialogRef } from '@angular/material/dialog';
import { CampaignServiceService } from 'src/app/core/services/campaign-service.service';
import { UserListNamesComponent } from '../user-list-names/user-list-names.component';

@Component({
  selector: 'app-view-campaign-component',
  templateUrl: './view-campaign-component.component.html',
  styleUrls: ['./view-campaign-component.component.scss']
})
export class ViewCampaignComponentComponent implements OnInit {
  public campaignId: any;
  public campaignName:any="";
  public campaignDescription:any="";
  public sms:boolean=false;
  public email:boolean=false;
  public notification:boolean=false;
  public whatsApp:boolean=false;
  public campaignDate:any;
  public campaignUsers:any=[]

  constructor(public dialogRef: MatDialogRef<ViewCampaignComponentComponent>,  public campService: CampaignServiceService, @Inject(MAT_DIALOG_DATA) public campaignData: any,
  public dialog: MatDialog, ) { }

  ngOnInit(): void {    
    this.campaignId = this.campaignData.campaignId
    this.getCampaignDataByCampaignId();
  }
  cancel(){
    this.dialogRef.close();
  }
  getCampaignDataByCampaignId() {
    this.campService.getCampaignBycampId({}, this.campaignId).then((res) => {
      console.log("camapaign View Data",res)
      this.campaignName= res.data[0].name;
      this.campaignDescription= res.data[0].description;
      this.sms=res.data[0].sms
      this.email=res.data[0].email
      this.notification=res.data[0].notification
      this.whatsApp=res.data[0].whatsapp
      this.campaignDate=res.data[0].createdAt
      res.data[0].campaignUsers.map((item)=>{
        this.campaignUsers.push(item.name)
      
      })
      console.log(this.campaignUsers)
    
     })
   
    }


    viewUserListCampaign(campaignUser){
      const dialogRef = this.dialog.open(UserListNamesComponent, {
        width: '25%',
        height: 'auto',
        data: { campaignUser: campaignUser },
        panelClass: 'custom-dialog-editcampaign-class',
        disableClose: true,
      });
  
      dialogRef.afterClosed().subscribe(result => {
  
      });

    }

  
   


}
