import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';

@Component({
  selector: 'app-campaign-router-component',
  templateUrl: './campaign-router-component.component.html',
  styleUrls: ['./campaign-router-component.component.scss']
})
export class CampaignRouterComponentComponent implements OnInit {

  constructor(private router: Router) { }

  ngOnInit(): void {
  }

  isLinkActive(route: string): boolean {
    return this.router.isActive(route, true);
  }
}
