import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { CampaignRouterComponentComponent } from './campaign-router-component/campaign-router-component.component';
import { ActivePageComponent } from './active-page/active-page.component';
import { DraftPageComponent } from './draft-page/draft-page.component';
import { EndedPageComponent } from './ended-page/ended-page.component';

const routes: Routes = [
  {
    path: '',
    component: CampaignRouterComponentComponent,
    children: [
      {
        path: '', 
        redirectTo: 'active-page',
        pathMatch: 'full'
      },
      {
        path: "active-page",
        component: ActivePageComponent,
      },
      {
        path: "draft-page",
        component: DraftPageComponent,
      },
      {
        path: "ended-page",
        component: EndedPageComponent,
      },
    ],
    pathMatch: 'prefix',
    data: {
      routerLinkActiveOptions: { exact: true, pathMatch: 'full' }
    }
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class CampaignRoutingModule { }
